import {Container, Row, Col, Navbar} from 'react-bootstrap';
import { Link } from 'react-router-dom';

function Winter(){
  return(
  <div className='section winter'>
   <Container fluid>
     <Row className='winter'>
        <Col sm={6}>
            <div className='winterimage'>
                <img src='hero_man.png.webp'/>
            </div>
        </Col>

        <Col sm={6}>
            <div className='wintercontent'>
                <h3>60% Discount</h3>
                <h1>Winter Collection</h1>
                <p>Best Cloth Collection By 2020!</p>
                <Link as={Link}to='/shopPage' className='shopbtn'>Shop Now</Link>
                {/*we use Link in Replace of <a></a>Tag in ROUTING...*/}
            </div>
        </Col>
     </Row>
   </Container> 
 </div>

  );
}
export default Winter;