import {Container, Row, Col, Navbar} from 'react-bootstrap';
import { Link } from "react-router-dom";
import axios from "axios";
import { useState, React, useEffect } from "react";
import { useParams } from "react-router-dom"; 

export default function ApiProductSingleView() {

  const {id}=useParams();
  const[fetchData1,setFetchData1]=useState([]);

  const fetchData=()=>{
    axios.get(`https://619781e15953f10017d23d9f.mockapi.io/story/story1/${id}`)
    .then(function(response)
    {
        setFetchData1(response.data);
    }
    )
    .catch(function(error)
    {
        console.log(error);
    }
    )  
}
  useEffect(()=>{
    fetchData();
  },[])

    return (
    <div className='view1'>
      <h4>PRODUCT DETAILS</h4>
      <p>Product Name: {fetchData1.title} </p>
      <p>Actual Price: {fetchData1.actualPrice} </p>
      <p>Offer Price: {fetchData1.offerPrice} </p>
    </div>
  )
}
