import { useState, React, useEffect } from "react";
import {Container, Row, Col, Navbar} from 'react-bootstrap';
import { Link } from "react-router-dom";
import axios from "axios";

export default function ApiProject2AddUser() {
  
  var data1 = {
    userName: '',
    age: '',
    phonNo: '',
    address: ''
  }

  const[userData,setUserData]=useState(data1);

  function changeData(e){
    setUserData({...userData,[e.target.name]:e.target.value})
  }

  const submitUser=(e)=>{
    e.preventDefault();
    axios.post('https://6165722bcb73ea001764200d.mockapi.io/students',userData)
    .then(function(response)
    {
        alert('User Added...');
    }
    )  
    .catch(function(error)
    {
        console.log(error);
    }
    )
}
  
    return (
    <div>
       <h3>Add User</h3>
       <form onSubmit={submitUser}>
        <label>Username: {userData.userName} </label><br/>
        <input type="text" name="userName" id="userName" onChange={changeData}/><br/>

        <label>Age: {userData.age} </label><br/>
        <input type="text" name="age" id="age" onChange={changeData}/><br/>

        <label>Phone No: {userData.phonNo} </label><br/>
        <input type="text" name="phonNo" id="phonNo" onChange={changeData}/><br/>

        <label>Address: {userData.address} </label><br/>
        <input type="text" name="address" id="address" onChange={changeData}/><br/>
       
        <button type="submit">AddUser</button>
       </form>
    </div>
  )
}
