import {Container, Row, Col, Navbar} from 'react-bootstrap';

var dataprovide = [
    { image: 'java.png', para : 'Java', isColor: 'blue' ,id: 1 },
    { image: 'phython.png', para : 'Python',isColor: 'blue' ,id: 2  },
    {image: 'c++.png', para : 'C++', isColor: 'pink' ,id: 3  },
    { image: 'reactJS.png', para : 'ReactJs', isColor: 'blue' ,id: 4  },
    {image: 'javasc.png', para : 'Javascript', isColor: 'pink' ,id: 5  },
    { image: 'html.png', para : 'HTML/CSS', isColor: 'blue' ,id: 6  },
    { image: 'flutter.png', para : 'Flutter', isColor: 'pink' ,id: 7  },
    { image: 'php.png', para : 'Php', isColor: 'blue' ,id: 8  },
    {image: 'wordPress.png', para : 'WordPress', isColor: 'pink' ,id: 9  },
    
]

function WeProvide(){

    return(
    <div className='section weProvide'>
        <Container fluid>
            <Row className='rowWeProvide'>
                <Col sm={12}>
                    <div className='weProvideHead'>
                        <h5>TOP CATEGORIES</h5>
                        <h3>What We Provide</h3>
                    </div>
                </Col>
            </Row>

            <Row className='rowWeProvide' key = {dataprovide.id}>
               { dataprovide.map((data1)=>
                <Col sm={2}>
                    { data1.isColor=='blue' ?
                    <div className= 'blue'>
                        <img src={data1.image}/>
                        <p>{data1.para}</p>
                        <a href='' className='arrowbtn'>
                        <i class="fa-solid fa-arrow-right"></i>
                        </a>
                        
                    </div> : 
                    <div className='pink'>
                        <img src={data1.image}/>
                        <p>{data1.para}</p>
                        <a href='' className='arrowbtn'>
                        <i class="fa-solid fa-arrow-right"></i>
                        </a>
                    </div> 
                          }
                </Col>
               ) 
};
            </Row>
        </Container>
    </div>
    );
}
export default WeProvide;