import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Form from 'react-bootstrap/Form';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import { Outlet, Link } from "react-router-dom"; //Router Import...

function Navbar1() {
  return (
    <div className='section navbar1'>
    <Navbar bg="light" expand="lg">
      <Container fluid className='mainnav'> 
        <Navbar.Brand href="#" className='estore'>Estore.</Navbar.Brand>
        <Navbar.Toggle aria-controls="navbarScroll" />
        <Navbar.Collapse id="navbarScroll">
          <Nav
            className="me-auto my-2 my-lg-0 nav1"
            style={{ maxHeight: '100px' }}
            navbarScroll
          >
            <Nav.Link as={Link}to='/bestPage'>Home</Nav.Link>
            <Nav.Link  as={Link} to ="/aboutUs">Categori</Nav.Link>
            <NavDropdown title="Latest" id="navbarScrollingDropdown">
              <NavDropdown.Item as={Link}to='/shopPage'>Product list</NavDropdown.Item>
              <NavDropdown.Item as={Link}to='/multiImg'>
                Product Details
              </NavDropdown.Item>
             </NavDropdown> 
              
             <NavDropdown title="Blog" id="navbarScrollingDropdown">
              <NavDropdown.Item as={Link} to='/latestPage'>Blog</NavDropdown.Item>
              <NavDropdown.Item href="#action6">
                Blog Details
              </NavDropdown.Item>
             </NavDropdown> 

             <NavDropdown title="Pages" id="navbarScrollingDropdown">
              <NavDropdown.Item href="#action7">Login</NavDropdown.Item>
              <NavDropdown.Item as={Link}to='/collectPage'>
                Card
              </NavDropdown.Item>
             
             <NavDropdown.Item href="#action9">Element</NavDropdown.Item>
              <NavDropdown.Item as={Link}to='/footer'>
                About
              </NavDropdown.Item>
              <NavDropdown.Item href="#action11">Confirmation</NavDropdown.Item>
              <NavDropdown.Item href="#action12">
                Shopping Cart
              </NavDropdown.Item>
             </NavDropdown> 
              <Nav.Link as = {Link} to='/mailTo'>Contact</Nav.Link>
          </Nav>
          <Form className="d-flex nav2">
            <Form.Control
              type="search"
              placeholder="Search"
              className="me-2"
              aria-label="Search"
            />
            <Button variant="outline-success">Search</Button>
          </Form>

          <ul className='nav3'>
            <li><i class="far fa-heart"></i></li>
            <li><i class="fas fa-shopping-cart"></i></li>
            <li><Link as={Link}to='/offers' className='signbtn'>Sign In</Link></li>
          </ul>
        </Navbar.Collapse>
      </Container>
    </Navbar>
   </div> 
  );
}

export default Navbar1;