import { Link } from 'react-router-dom';
import axios from "axios";
import { useState, React, useEffect } from "react";


export default function ApiFetchProductProject4() {
  
  const[getProduct,setProduct]=useState([]);

  function fetchData(){
    axios.get('https://619781e15953f10017d23d9f.mockapi.io/story/story1')
    .then(function(response)
    {
        setProduct(response.data);
    }
    )
    .catch(function(error)
    {
        console.log(error);
    }
    )
  }
  
  useEffect(()=>{
    fetchData();
  },[])

  const delProduct= (id)=>{
   axios.delete(`https://619781e15953f10017d23d9f.mockapi.io/story/story1/${id}`)
   .then(function(response)
   {
    fetchData();
    alert('Product Deleted...');
   }
   )
   .catch(function(error)
   {
    console.log(error);
   }
   )
  }

    return (
    <div>
        <h3>PRODUCTS Data Received : </h3>
        <table>
            <div className='table1'>
                <tr>
                    <th>Title</th>
                    <th>Image</th>
                   
                    <th>Action</th>
                </tr>

                {
                    getProduct.map((obj)=>(
                        <tr>
                            <td>{obj.title} </td>
                            <td className='tdImage'><img src={obj.images}/></td>
                            <td>
                                <Link to={`/ApiProject4SingleView/${obj.id}`} as={Link} className='viewBtn'>View</Link>
                                <Link className='viewBtn'>Edit</Link>
                                <button type='submit' onClick={()=>delProduct(obj.id)}>Delete</button>
                            </td>
                        </tr>
                    )
                    
                    )
                }
            </div>
        </table>
    </div>
  )
}
