import {Container, Row, Col, Navbar} from 'react-bootstrap';

function Shop(){
    var shoplist = [
     {"classes" : "colshop cat1", "head" : "Owmen's", "link" : "Best New Deals",
       "para" : "New Collection"},
       {"classes" : "colshop cat2", "head" : "Winter Cloth", "head3" : "New Collection",
       "para" : "Discount!"},
       {"classes" : "colshop cat3", "head" : "Man's Cloth", "link" : "Best New Deals",
       "para" : "New Collection"}, 
    ];

    return(
   <div className='section shop'>
    <Container fluid>
       <h2>Shop by Category</h2> 
        <Row>
           { shoplist.map ((shops) =><Col sm={4}>
            <div className={shops.classes}>
               
                <div className='colshopcontent'>
                    <h2>{shops.head}</h2>
                    <a href='' className='bestbtn'>{shops.link}</a>
                    <p>{shops.para}</p>
                    <h3>{shops.head3}</h3>
                </div>
            </div>
            </Col> 
           )
           };      
            {/* <Col sm={4}>
            <div className='colshop cat2'>
                
                <div className='colshopcontent'>
                    <p>Discount!</p>
                    <h2>Winter Cloth</h2>
                    <h3>New Collection</h3>
                    
                </div>
            </div>
            </Col> 

            <Col sm={4}>
            <div className='colshop cat3'>
                
                <div className='colshopcontent'>
                    <h2>Man's Cloth</h2>
                    <a href='' className='bestbtn'>Best New Deals</a>
                    <p>New Collection</p>
                </div>
            </div>
            </Col>  */}
        </Row>
    </Container>
   </div>
  );
}
export default Shop;