import { useState, React, useEffect } from "react";
import {Container, Row, Col, Navbar} from 'react-bootstrap';
import { Link } from "react-router-dom";
import axios from "axios";
import { useParams } from "react-router-dom"; 

export default function ApiProject1View() {

 const {id}=useParams();
 const[fetchData1,setFetchData1]=useState([]);

  const fetchData=()=>{
    axios.get(`https://6165722bcb73ea001764200d.mockapi.io/students/${id}`)
    .then(function(response)
    {
        setFetchData1(response.data);
    }
    )
    .catch(function(error)
    {
        console.log(error);
    }
    )
  }   

 useEffect(()=>
 {
    fetchData();
 },[]
 )

    return (
    <div className="view1">
        <h4>User Details</h4>
        <p>Name: {fetchData1.userName} </p>
        <p>Age: {fetchData1.age} </p>
        <p>Address: {fetchData1.address} </p>
        <p>Phone No: {fetchData1.phonNo} </p>
    </div>
  )
}
