import { useState, React, useEffect } from "react";
import axios from "axios";
import { useParams } from "react-router-dom";


export default function SingleUserApi22() {
 
 const {id}=useParams();
 const[fetchData1,setFetchData1]=useState([]);

 const fetchData=()=>{
    axios.get(`https://6165722bcb73ea001764200d.mockapi.io/students/${id}`)
    .then(function(response)
    {
        setFetchData1(response.data);
    }
    )
    .catch(function(error)
    {
        console.log(error);
    }
    )
 }
 useEffect(()=>
 {
    fetchData();
 },[]
 )

    return (
    <div className="view1">
         <h4>Student Info </h4>
       <p>Username: {fetchData1.userName} </p>
       <p>Age: {fetchData1.age} </p>
       <p>Class: {fetchData1.class} </p>
       <p>Roll No: {fetchData1.rollNo} </p>
       <p>Phone No: {fetchData1.phonNo} </p>
       <p>Address: {fetchData1.address} </p>
       <button type="submit">Edit Details</button>   
    </div>
  )
}
