import {Container, Row, Col, Navbar} from 'react-bootstrap';

function Offers(){
 return(
  <div className='section offers'>
    <Container fluid>
        <Row className='offers'>
            <Col sm={6}>
                <div className='coloffers'>
                    <h1>Get Our
                        Latest Offers News</h1>
                    <p>Subscribe news latter</p>    
                </div>
            </Col>

            <Col sm={6}>
                <div className='coloffers'>
                   <form className='offersform'>
                     <input type='search' placeholder='Your Email Here' className='formholder' >

                     </input>
                     <a href='' className='shopbtn offers'>Shop Now</a>
                   </form>
                </div>
            </Col>
        </Row>
    </Container>
  </div>
 );
}
export default Offers;
