
import { useState, React, useEffect } from "react";
import axios from "axios";
import { Link } from "react-router-dom";


export default function ApiProject1AddUser() {

   var data1={
      userName: '',
      age: '',
      address: '',
      phonNo: ''
   };
   const[formData,setFormData]=useState(data1);
   const[fetchData1,setFetchData1]=useState([]);

   const fetchData=()=>{
    axios.get('https://6165722bcb73ea001764200d.mockapi.io/students')
    .then(function(response)
    {
        setFetchData1(response.data);
    }
    )

    .catch(function(error)
    {
        console.log(error);
    }
    )
   }

   useEffect(()=>
   {
    fetchData();
   }
   )

   function changeValues(e){
    setFormData({...formData,[e.target.name]:e.target.value});
   }

   const submitForm=(e)=>{
    e.preventDefault();
    axios.post('https://6165722bcb73ea001764200d.mockapi.io/students',formData)
    .then(function(response)
    {
        alert('User Added...');
    }
    )
    .catch(function(error)
    {
        console.log(error);
    }
    )
   }

   

    return (
    <div>
        <h3>Add User Below: </h3>
        <form onSubmit={submitForm}>
            <label>Username: {formData.userName} </label><br/>
            <input type="text" name="userName" id="userName" onChange={changeValues}/><br/>

            <label>Age: {formData.age} </label><br/>
            <input type="text" name="age" id="age" onChange={changeValues}/><br/>

            <label>Address: {formData.address} </label><br/>
            <input type="text" name="address" id="address" onChange={changeValues}/><br/>

            <label>Phone No: {formData.phonNo} </label><br/>
            <input type="text" name="phonNo" id="phonNo" onChange={changeValues}/><br/>
        
            <button type="submit">AddData</button>
        </form>

        
    </div>
  )
}
