import {Container, Row, Col, Navbar} from 'react-bootstrap';

function Multiimg(){
  var imagelist = [
    {"image" : "gallery1.jpg.webp"},
    {"image" : "gallery2.jpg.webp"},
    {"image" : "gallery3.jpg.webp"},
    {"image" : "gallery4.jpg.webp"},
    {"image" : "gallery5.jpg.webp"},
   ];
 
  return(
  <div className='section multiimg'>
    
    <Container fluid>
        <Row className='multiimg'>
             {
              imagelist.map ((test2) => <img src= {test2.image}/>

              )
            }; 
       
        </Row>
    </Container>
    
  </div>
 );
}
export default Multiimg;