import axios from "axios";
import { useState, React, useEffect } from "react";


export default function ApiAddProductProject4() {
 
var product = {
   images: '',
   actualPrice: '',
   offerPrice: '',
   title: ''
};
    
const[productData,setProductData]=useState(product); 
 
  function changeProduct(e){
    setProductData({...productData,[e.target.name]:e.target.value});
  }
  const submitProduct=(e)=>{
    e.preventDefault();
    axios.post('https://619781e15953f10017d23d9f.mockapi.io/story/story1',productData)
    .then(function(response)
    {
        alert('Product Added...');
    }
    )
    .catch(function(error)
    {
        console.log(error);
    }
    )
  }

    return (
    <div className="addProduct">
        <h3>ADD PRODUCT FORM : </h3>
        
        <form onSubmit={submitProduct}>
            <label>Images: {productData.images} </label><br/>
            <input type="text" name="images" id="images" onChange={changeProduct}/><br/>

            <label>Actual Price: {productData.actualPrice} </label><br/>
            <input type="text" name="actualPrice" id="actualPrice" onChange={changeProduct}/><br/>

            <label>Offer Price: {productData.offerPrice} </label><br/>
            <input type="text" name="offerPrice" id="offerPrice" onChange={changeProduct}/><br/>

            <label>Title: {productData.title}  </label><br/>
            <input type="text" name="title" id="title" onChange={changeProduct}/><br/><br/>

            <button type="submit">AddProduct</button>
        </form>
    </div>
  )
}
